*, :before, :after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.header__box {
  width: 100%;
  height: 72px;
  background: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  padding: 12px 24px;
  display: flex;
  position: relative;
}

.logo {
  align-items: center;
  display: flex;
}

.logo__text {
  margin-left: 8px;
}

.header__singin {
  width: 183px;
  height: 48px;
  letter-spacing: -.04em;
  color: #fff;
  background: #4f2ee8;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

#toggle {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  height: 20px;
  width: 40px;
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 20px;
  outline: none;
  position: absolute;
  right: 230px;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

#toggle:after {
  content: "";
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.dark-theme {
  background-color: #202024;
}

.dark {
  background: #111;
  border: 1.5px solid #fff;
  border-radius: 0 0 8px 8px;
}

.dark__icon {
  fill: #f3f3f3;
}

.dark-theme #toggle {
  background: #4f2ee8 linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.dark-theme #toggle:after {
  top: 1px;
  left: 21px;
}

.register {
  max-width: 579px;
  height: 516px;
  background: #f6f6f6;
  border: 2px solid #111;
  border-radius: 18px;
  margin-left: auto;
  margin-right: auto;
  padding: 80px 40px 84px;
}

.register__form {
  max-width: 499px;
  flex-direction: column;
  gap: 28px;
  display: flex;
}

.register__form--input::-ms-input-placeholder {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
}

.register__form--input::placeholder {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
}

.register__form--thumb {
  max-width: 499px;
  outline: 1px solid tomato;
  position: relative;
}

.register__form--input {
  width: 100%;
  background-color: #f6f6f6;
  border: 2px solid #111;
  border-radius: 40px;
  padding: 20px 46px 20px 40px;
}

.register__form--icon {
  fill: #f6f6f6;
  stroke: #111;
  position: absolute;
  top: 50%;
  right: 18px;
  transform: translateY(-50%);
}

.register__form--button {
  width: 100%;
  color: #f3f3f3;
  cursor: pointer;
  letter-spacing: -.01em;
  text-transform: uppercase;
  background-color: #111;
  border: 2px solid #111;
  border-radius: 40px;
  padding: 20px 215px;
  font-weight: 700;
  line-height: 1.33;
}

